.header{

  

background-color:$verde;
  .contenido-header{
    padding: $separaciones / 2 0;
    
    @include tablet{    
        display: flex;
    justify-content: space-between;
    align-items: center;
       }
    
}

   h1 {
   color:$blanco;
   margin-bottom: 1rem;
   text-align: center;
   @include tablet{
       margin-bottom: 0;
   }
  }
  
  
}
.navegacion-principal{
  display: flex;
  gap: 2rem;  
  justify-content: center;
  a {
      color: $blanco;
      font-size: 2rem; 
      &:hover{
        color: $amarillo;
      } 
    }
    
}

.fijo{
  position: fixed;
  width: 100%;
  box-shadow: 0 .3rem .4rem #444 ;
  
  
}